@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    @apply border-gray-200;
  }
  
  body {
    @apply bg-white text-gray-900;
    font-feature-settings: "rlig" 1, "calt" 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  /* Allow text selection in input fields */
  input, textarea {
    -webkit-user-select: text;
    -khtml-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    user-select: text;
  }
}

@layer components {
  .block-shadow {
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  }
  
  .block-shadow-hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  }
  
  .tower-glow {
    box-shadow: 0 0 20px rgba(14, 165, 233, 0.3);
  }
  
  .focus-glow {
    box-shadow: 0 0 30px rgba(245, 158, 11, 0.5);
  }
  
  .energy-high {
    @apply bg-gradient-to-r from-green-400 to-green-500;
  }
  
  .energy-medium {
    @apply bg-gradient-to-r from-yellow-400 to-orange-400;
  }
  
  .energy-low {
    @apply bg-gradient-to-r from-red-400 to-red-500;
  }
  
  .category-work {
    @apply bg-gradient-to-r from-blue-500 to-blue-600;
  }
  
  .category-study {
    @apply bg-gradient-to-r from-purple-500 to-purple-600;
  }
  
  .category-exercise {
    @apply bg-gradient-to-r from-green-500 to-green-600;
  }
  
  .category-break {
    @apply bg-gradient-to-r from-yellow-500 to-orange-500;
  }
  
  .category-personal {
    @apply bg-gradient-to-r from-pink-500 to-pink-600;
  }
  
  .category-habit {
    @apply bg-gradient-to-r from-indigo-500 to-indigo-600;
  }
  
  .category-meeting {
    @apply bg-gradient-to-r from-cyan-500 to-cyan-600;
  }
  
  .category-creative {
    @apply bg-gradient-to-r from-rose-500 to-rose-600;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100 rounded-full;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-300 rounded-full hover:bg-gray-400;
}

/* Animation keyframes */
@keyframes blockPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

@keyframes towerRise {
  from { 
    transform: translateY(100px) scale(0.8);
    opacity: 0;
  }
  to { 
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

@keyframes completionGlow {
  0% { box-shadow: 0 0 5px rgba(34, 197, 94, 0.5); }
  50% { box-shadow: 0 0 25px rgba(34, 197, 94, 0.8); }
  100% { box-shadow: 0 0 5px rgba(34, 197, 94, 0.5); }
}

.block-pulse {
  animation: blockPulse 2s ease-in-out infinite;
}

.tower-rise {
  animation: towerRise 0.6s ease-out;
}

.completion-glow {
  animation: completionGlow 1s ease-in-out;
}

/* Mobile-specific styles */
@layer utilities {
  .mobile-safe-area {
    padding-bottom: env(safe-area-inset-bottom);
    padding-top: env(safe-area-inset-top);
    padding-left: env(safe-area-inset-left);
    padding-right: env(safe-area-inset-right);
  }
  
  .mobile-touch {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    user-select: none;
  }
  
  .mobile-scroll {
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
  }
  
  .mobile-focus {
    -webkit-tap-highlight-color: rgba(59, 130, 246, 0.1);
  }
  
  /* Prevent zoom on input focus */
  @media screen and (max-width: 768px) {
    input[type="text"],
    input[type="email"],
    input[type="password"],
    input[type="number"],
    input[type="tel"],
    input[type="url"],
    input[type="search"],
    textarea,
    select {
      font-size: 16px !important;
    }
  }
  
  /* Mobile gesture support */
  .swipeable {
    touch-action: pan-x pan-y;
  }
  
  .no-scroll {
    overflow: hidden;
    position: fixed;
    width: 100%;
    height: 100%;
  }
}